.coverBox {
    display: flex;
    flex-direction: column;
  }

  .input {
    display: flex;
    margin: 25px 0px 0px 50px;
  }

  .button {
    width: 100px;
    height: 25px;
    margin-left: 50px;
    color: cornsilk;
    background-color: rgb(71, 114, 141);
    margin: 25px 0px 0px 50px;
    border: 1px;
  }
  
  input:invalid ~ span:after {
    content: '✖';
    padding-left: 5px;
    position: absolute;
  }
  
  input:valid ~ span:after {
    content: '✓';
    padding-left: 5px;
    position: absolute;
  }
   
  label {
    display: flex;
    flex-direction: row;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
  }
  